@import '../common/sass/variables';

*,
*::after,
*::before {
	box-sizing: border-box;
}

.footer {
	height: 100%;
	background-color: $bgSecondColor;

	.footerContainer {
		display: flex;
		flex-direction: column;
		flex-wrap: nowrap;
		justify-content: space-around;

		.infoBox {
			display: flex;
			justify-content: center;
			gap: 10px;
			margin-top: 2em;
			margin-bottom: 2em;
			@media screen and (max-width: 896px) {
				flex-direction: column;
				justify-content: center;
				align-items: center;
				width: 100%;
			}

			.form {
				background-color: $bgColor;
				padding: 1em;
				width: 200px;
				height: 170px;
				display: flex;
				flex-direction: column;
				justify-content: center;
				@media screen and (max-width: 896px) {
					width: 60%;
				}

				.iconBlock {
					text-align: center;

					.infoIcons {
						color: white;
						font-size: 30px;
					}
				}

				.mainText {
					text-align: center;
					font-family: $fontFamily;
					font-size: 20px;
					color: $primaryColor;
					margin-bottom: 5px;
					font-weight: 600;
					line-height: 1.2;
					text-transform: capitalize;
					margin-top: 30px;
				}

				.paragraph {
					font-family: $fontFamily;
					font-size: 16px;
					margin: 0;
					text-align: center !important;
					color: #6c757d;
					font-weight: 400;
					line-height: 1.6;
				}

				&:hover {
					border: 2px solid #c79864;
				}
			}
		}

		.title {
			background-color: powderblue;
		}

		.wrapper {
			display: flex;
			flex-direction: column;
			width: 100%;
			align-items: center;

			.socialMenu {
				width: 100%;
				display: flex;
				justify-content: center;
				align-items: center;

				.icon {
					height: 60px;
					width: 60px;
					text-align: center;
					margin-inline: 2%;

					.iconStyle {
						color: $primaryColor;
						font-size: 35px;
					}
				}
			}

			.copyWriter {
				color: white;
				font-family: $fontFamily;
				padding: 10px;
			}
		}
	}
}




